<template>
  <div v-if="isLoading">loading</div>
  <div v-else class="weui-cells">
    <template>
      <!---->
      <a v-for="(item,index) in curDepartInfo.departList" class="weui-cell weui-cell_access">
        <div class="weui-cell__hd">
          <label v-bind:for="item.value">
            <input type="checkbox" class="" v-bind:id="item.value" v-bind:value="item.value"
                   v-bind:checked="isAllChecked||item.isChecked"
                   v-on:change="toggleDepart(item,$event)"/>
            <i class="weui-icon-checked"></i>
          </label>
        </div>
        <div class="weui-cell__bd" v-on:click="routerToChild(item,index)">
          <p>{{item.title}}</p>
        </div>
        <div class="weui-cell__ft" v-on:click="routerToChild(item,index)"></div>
      </a>
    </template>
  </div>
</template>
<script>
  import request from '../utils/request'
  import consts from '../mock-data/consts'
  import storage from '../utils/storage'

  export default {
    name: 'choose-depart',
    data: function () {
      return {
        departInfo: {},
        id: parseInt(this.$route.params.id),
        path: this.$route.params.path
      }
    },
    created: function () {
      this.requireAllDepartList()
    },
    $route (to, from) {
      this.requireAllDepartList()
    },
    methods: {
      /**
       * 
       */
      getAllParentIds: function (depart, ids) {
        if (typeof (depart.parentDepart.value) === 'number') {
          ids.push(depart.parentDepart.value)
          this.getAllParentIds(depart.parentDepart, ids)
        } else {
          return ids
        }
      },
      /**
       * depart
       */
      getAllChildIds: function (depart, ids) {
        if (depart.departList.length > 0) {
          for (let childDepart of depart.departList) {
            ids.set(childDepart.value)
            this.getAllChildIds(childDepart, ids)
          }
        } else {
          return ids.set(depart.value)
        }
      },
      getAllLastChild: function (depart, ids) {
        if (depart.departList.length > 0) {
          for (let childDepart of depart.departList) {
            this.getAllLastChild(childDepart, ids)
          }
        } else {
          return ids.set(depart.value)
        }
      },
      toggleDepart: function (depart, event) {
        let isAdd = event.target.checked
        if (isAdd) {
          //
          this.addChoseDepart(depart)
        } else {
          //
          this.delChoseDepart(depart)
        }
      },
      addChoseDepart: function (depart) {
        storage.toggleValueInSessionMap(consts.KEY_CHOOSE_DEPARTS, depart.value, depart.title, true)
        let ids = new Set()
        let allChooseDeparts = storage.getSessionSet(consts.KEY_ALL_CHOOSE_DEPARTS)
        this.getAllChildIds(depart, ids)
        for (let id of ids) {
          allChooseDeparts.set(id)
        }
        storage.setSessionStorage(consts.KEY_ALL_CHOOSE_DEPARTS, allChooseDeparts)
//        this.setDepartStatus()
      },
      delChoseDepart: function (depart) {
        storage.toggleValueInSessionMap(consts.KEY_CHOOSE_DEPARTS, depart.value, depart.title, false)
        let ids = new Set()
        let allChooseDeparts = storage.getSessionSet(consts.KEY_ALL_CHOOSE_DEPARTS)
        this.getAllParentIds(depart, ids)
        for (let id of ids) {
          allChooseDeparts.delete(id)
        }
        storage.setSessionStorage(consts.KEY_ALL_CHOOSE_DEPARTS, allChooseDeparts)
      },
      requireAllDepartList: function () {
        let com = this
        com.isLoading = true
        //
        if (storage.getSessionArray(consts.KEY_DEPARTS_CHILDREN_TREE).length > 0) {
          com.childrenTree = storage.getSessionArray(consts.KEY_DEPARTS_CHILDREN_TREE)
          com.getCurDepartInfo()
          return
        }
        //
        request.getDepartList(function (response) {
          console.log('depart-person' + JSON.stringify(response))
          com.childrenTree = com.getChildrenTree(response)
          com.getCurDepartInfo()
        })
      },
      /**
       *
       */
      getCurDepartInfo: function () {
        let pathArr = this.path.split('-')
        this.curDepartInfo = this.getNodeInTree(this.childrenTree, pathArr)
        this.setDepartStatus()
      },
      setDepartStatus: function () {
        let allChooseDeparts = storage.getSessionSet(consts.KEY_ALL_CHOOSE_DEPARTS)
        for (let childDepart of  this.departInfo.departList) {
          if (allChooseDeparts.has(childDepart.value)) {
            childDepart.isChecked = true
          } else {
            let childIds = new Set()
            this.getAllLastChild(childDepart, childIds)
            if (Array.from(childIds).every(function (childId) {
                return allChooseDeparts.has(childId)
              })) {
              childDepart.isChecked = true
              allChooseDeparts.set(childDepart.value)
            } else {
              childDepart.isChecked = false
            }
          }
        }
        storage.setSessionStorage(consts.KEY_ALL_CHOOSE_DEPARTS, allChooseDeparts)
        this.isLoading = false
      },
      /**
       * tree
       * @param tree
       * @param pathArr 
       * @returns {*} departInfo
       */
      getNodeInTree: function (tree, pathArr) {
        if (pathArr.length === 1) {
          console.log('node' + JSON.stringify(tree[pathArr[0]]))
          return tree[pathArr[0]]
        } else {
          this.getNodeInTree(tree[pathArr[0]], pathArr.splice(0, 1))
        }
      },
      /**
       * arraytree
       * @param nodes 
       * @returns {Array} tree
       */
      getChildrenTree: function (nodes) {
        if (typeof (nodes) === 'undefined' || nodes.length === 0) {
          return []
        }
        nodes.sort(function (a, b) {
          return a.value - b.value
        })
        let map = {},
          node, roots = []
        for (let i = 0; i < nodes.length; i++) {
          node = nodes[i]
          node.departList = []
          node.personList = []
          map[node.value] = i // use map to look-up the parents
          if (node.parentvalue > 0) {
            if (node.parentvalue === 1) {
              nodes[map[-1]].departList.push(node)
            } else {
              nodes[map[node.parentvalue]].departList.push(node)
            }
          } else {
            roots.push(node)
          }
        }
        console.log('' + JSON.stringify(roots))
        return roots
      },
      getParentsTree: function (nodes) {
        if (!nodes || nodes.length === 0) {
          nodes = this.nodes
        }
        nodes.sort(function (a, b) {
          return a.value - b.value
        })
        let map = {},
          node,
          roots = []
        for (let i = 0; i < nodes.length; i++) {
          node = nodes[i]

          map[node.value] = i // use map to look-up the parents
          roots.push(node)
          if (node.parentvalue > 0) {
            if (node.parentvalue === 1) {
              nodes[i].parentDepart = nodes[map[-1]]
            } else {
              nodes[i].parentDepart = nodes[map[node.parentvalue]]
            }
          }
        }
        console.log('' + JSON.stringify(roots))
        return roots
      },
      routerToChild: function (item, index) {
        router.push({
          name: 'choose-depart',
          params: {
            id: item.value,
            path: path + '-' + index
          }
        })
      }
    }
  }
</script>
<style scoped>
  input[type=checkbox] {
    position: absolute;
    z-index: -9999;
    opacity: 0;
  }

  .weui-icon-checked:before {
    content: '\EA01';
    color: #C9C9C9;
    font-size: 23px;
    display: block;
  }

  input[type=checkbox]:checked + .weui-icon-checked:before {
    display: inline-block;
    content: '\EA06';
    color: #09BB07;
    font-size: 23px;
  }

  .weui-cells {
    margin-top: 0;
  }
</style>



